nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  white-space: nowrap;
  position: fixed;
  top: 0;
  width: 90%;
  height: 15vh;
  color: $white;
  background-color: rgba(0, 0, 0, 0);
  font-weight: $heavy-font;
  padding: 0 $container-margin-x;
  // transition for onscroll background color change
  transition: 0.7s ease-in-out;
  z-index: 9999; }

.nav-scrolled {
  background-color: $blue;
  height: 10vh;
  box-shadow: 0 1px 4px rgba(0, 7, 64, .6); }

.nav-links {
  li {
    padding: 1rem 1rem;
    border-radius: 5px;
    transition: .3s ease-in-out; } }

.nav-brand {
  width: 100px;
  //float: left
 }  //padding-left: $container-margin-x

.logo {
  transition: .7s ease-in-out; }

.nav-img {
  transition: .7s ease-in-out; }

.nav-links {
  //padding-right: $container-margin-x
  display: flex;
  font-size: 1.125rem;
  text-decoration: none;
  list-style: none;
  a {
    // we need a better way of spacing out the links
    margin-left: 10px;
    // transition for when we hover over a link and fade out the others
    transition: 0.2s ease-in-out; } }

.burger {
  display: none; }

@media screen and ( min-width: 1024px ) {
  .nav-links:hover > a {
    opacity: 0.5; }

  .nav-links:hover > a:hover {
    opacity: 1;
    li {
      background-color: $vivid-red; } } }

@media screen and ( max-width: 1024px ) {
  nav {
    height: 10vh;
 }    //background-color: rgba(0,0,0,1)


  .logo-text {
    display: none; }

  .logo {
    height: 10vh; }

  .logo-bot {
    transform: translate(-60px, 25%);
    z-index: 100; }

  .nav-links {
    position: absolute;
    right: 0;
    top: 0;
    margin: 0;
    padding: 0;
    width: 95vw;
    height: 80vh;
    flex-direction: column;
    align-self: baseline;
    border-bottom-left-radius: 40px;
    padding: 20vh 0 0 5vw;
    background-color: $black;
    color: $white;
    z-index: -1;
    // transition for the whole slider
    transform: translateX(100%);
    transition: transform 0.5s ease-in-out; }

  .nav-active {
    // we toggle this class in mobile-nav-slider.js on burger click
    transform: translateX(0%); }

  .nav-links li {
    opacity: 0;
    vertical-align: top;
    padding-bottom: 1vh;
    margin-bottom: 6vh;
    border-bottom: 1px solid rgb(130, 130, 130);
    a {
      margin: 0;
      padding: 0; } }

  .burger {
    padding-right: $container-margin-x;
    display: block;
    cursor: pointer; }

  .burger-line-1, .burger-line-2, .burger-line-3 {
    background-color: $white;
    width: 25px;
    height: 1px;
    margin: 7px; }

  .burger-line-1 {
    transition: all 0.6s ease-in-out; }

  .burger-line-2 {
    transition: all 0.3s ease-in-out;
    // delay on closing
    transition-delay: 0.3s; }

  .toggle {
    .burger-line-1 {
      width: 8px;
 } }      // reduce by 17 px

  .toggle {
    .burger-line-2 {
      width: 17px;
      // reduce by 8 px
      // no delay on opening
      transition-delay: 0s; } }

  // animation for the links as the slider opens
  @keyframes navLinksFade {
    // the timings for this are set in mobile-nav-slider.js
    from {
      opacity: 0.1;
      transform: translateX(200px); }
    to {
      opacity: 1;
      transform: translateX(0); } } }
